function init(){

    if(localStorage.getItem('token') !== null) {
        fetch(URL_BASE + '/api/verifytoken', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': localStorage.getItem('token'),
                'Device-Id': localStorage.getItem('fingerprint'),
                'Device-App': localStorage.getItem('app'),
                'Device-Info': localStorage.getItem('browser')
            }
        }).then(function (response) {

                if (response.status === 200) {
                    var level = response.headers.get('Level');

                    if (level === "ORG") window.location.href = "../Dashboards/DashboardOrganizations.html";
                    else if (level === "ADMIN") window.location.href = "../Dashboards/DashboardAdmin.html";

                } else if (response.status === 403) {
                    console.log("403 (no token)");
                    ReactDOM.render(
                        <InitPage/>, document.getElementById("root")
                    );
                } else {
                    console.log("Error 417 (formatacao)");
                    ReactDOM.render(
                        <InitPage/>, document.getElementById("root")
                    );
                }


            }
        )
            .catch(function (err) {
                console.log('Fetch Error', err);
            });
    }else{
        ReactDOM.render(
            <InitPage/>, document.getElementById("root")
        );
    }

}

window.onload = function() {
    var millisecondsToWait = 500;
    setTimeout(function() {
        if(localStorage.getItem("fingerprint") === null) {
            fingerprintjs2().get(function (result, components) {
                localStorage.setItem("fingerprint", result);
                console.log(result);
            })
        }

        if(localStorage.getItem("app") === null){
            localStorage.setItem("app","Web");
        }

        if(localStorage.getItem("browser") === null){
            fingerprintjs2().get(function (result, components) {
                localStorage.setItem("browser",components[0].value)            })
        }
        init();
    }, millisecondsToWait);

};



